export default {
  name: 'Prompt',
  props: {
    icon: {
      type: String,
      default: ''
    },
    windowHeight: {
      type: String,
      default: '100%'
    },
    title: String,
    text: String,
    visible: {
      type: Boolean,
      default: true
    },
    bgColor: {
      type: String,
      default: ''
    },
    inlineBlock: {
      type: Boolean,
      default: false
    },
    type: {
      validator (value) {
        return ['nomore', 'nodata', 'noorder', 'nologin', 'noimage', 'nocoupon', 'nomessage', 'nofinish', 'nonet'].indexOf(value) !== -1
      },
      type: String,
      default: ''
    }
  },
  computed: {
    typeImage () {
      const typeImage = {
        'nomore': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/nonorder_v1.png',
        'noorder': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/nonorder_v1.png',
        'nologin': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/nonlogin_v1.png',
        'nothing': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/nonthing_v1.png',
        'nonet': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/nonnet_v1.png',
        'nocoupon': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/noncoupon_v2.png',
        'nomessage': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/nonmessage_v1.png',
        'nofinish': 'https://cloud-minapp-31286.cloud.ifanrusercontent.com/nonfinished_v1.png'
      }
      return typeImage[this.type]
    }
  },
  methods: {
    onPrompt () {
      this.$emit('click')
    }
  }
}
